import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from '@material-ui/pickers';
import DateFnsUtils from "@date-io/date-fns";
import { ptBR } from 'date-fns/locale';
import Switch from 'react-switch';
import Select from 'react-select';
import NumberFormat from 'react-number-format';

import Load from '../../components/Load/Load';
import Swal from '../../components/SweetAlert/SwetAlert';

import ImageProfile from '../../assets/images/user.png';

import './styles.scss';
import api from '../../services/api';

import util from '../../services/util.js';

export default function ModalUser(props) {
  const history = useHistory();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [emailCtrl, setEmailCtrl] = useState('');
  const [user, setUser] = useState('');
  const [userCtrl, setUserCtrl] = useState('');
  const [password, setPassword] = useState('');
  const [passwordConfirm, setPasswordConfirm] = useState('');
  const [phone, setPhone] = useState('');
  const [birth, setBirth] = useState(new Date());
  const [cep, setCep] = useState('');
  const [street, setStreet] = useState('');
  const [number, setNumber] = useState('');
  const [complement, setComplement] = useState('');
  const [district, setDistrict] = useState('');
  const [city, setCity] = useState('');
  const [state, setState] = useState('');
  const [optState, setOptState] = useState([]);
  const [loading, setLoading] = useState(false);
  const [userId, setUserId] = useState(0);
  const [isAdm, setIsAdm] = useState(false);
  const [addressId, setAddressId] = useState(0);

  const token = localStorage.getItem('token');

  useEffect(() => {
    //recupera informaes bsicas para construir as opes nos inputs
    getInfo();

    //atualizar/detalhar usurio (chamada vem do dropmenu de detalhes)
    if (props.location.state) {
      disableInputPswd(true); //desativa campos de senha no update
      getUser();
    }
    else disableInputPswd(false);
  }, [])

  async function getInfo() {
    setLoading(true);

    try {
      let resp = await api.get('/address/state', {
        headers: { token }
      });

      resp = resp.data
      let tmp = [];
      (resp.response).forEach(el => {
        tmp.push({ value: el.code, label: el.name })
      });
      setOptState(tmp)

    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  }

  async function getUser() {
    const { id } = props.location.state;

    setLoading(true);
    try {
      let resp = await api.get(`/user/${id}`, {
        headers: { token }
      });

      resp = resp.data

      if (resp.status) {
        const { response } = resp;
        const { Address } = response;

        setUserId(response.id);
        setName(response.name);
        setBirth(new Date(response.birth));
        setEmail(response.email);
        setEmailCtrl(response.email);
        setPhone(response.phone);
        setUser(response.user);
        setUserCtrl(response.user);
        setIsAdm(response.isAdm);

        setAddressId(Address.id);
        setCep(Address.cep ? Address.cep : '');
        setStreet(Address.street ? Address.street : '');
        setNumber(Address.number ? Address.number : '');
        setComplement(Address.complement ? Address.complement : '');
        setDistrict(Address.district ? Address.district : '');
        setCity(Address.city ? Address.city : '');
        setState(Address.state ? { value: Address.state, label: Address.state } : '');
      }
      else {
        Swal.swalErrorInform();
      }

    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  }

  async function handleSubmit(event) {
    event.preventDefault();
    setLoading(true);

    const stateTmp = state.value;
    const data = {
      'user': { name, email, user, password, phone, birth, isAdm },
      'address': { cep, 'state': stateTmp, city, district, street, complement, number }
    }

    try {
      let query = null;

      if (userId > 0) {
        delete data.user.password; //atualizao deste modal no envia senha
        data.address.id = addressId;

        query = await api.put('/user/' + userId, data, {
          headers: { token },
        });
      }
      else {
        query = await api.post('/user', data, {
          headers: { token },
        });
      }

      if (query.data.status) {
        Swal.swalInform();

        closeModal();
      }
      else errorDisplay();
    }
    catch (error) {
      errorDisplay();
      console.log(error);
    }

    setLoading(false);
  }

  async function handleCheckUser() {
    //se userCtrl no estiver vazio, ele armazena o user da atualizao 
    //verifica-se se o user na entrada atual  diferente do anterior, 
    //para evitar validar um user existente que j pertence ao usurio atual
    if (userCtrl === '' || userCtrl !== user) {
      setLoading(true);
      try {
        const query = await api.get('/user/byuser', {
          data: {},
          params: { user },
          headers: { token }
        });

        const resp = query.data;
        const div = document.getElementById('user');

        if (resp.response) div.setCustomValidity("Usurio j em uso.");
        else div.setCustomValidity("");

      } catch (error) {
        console.log(error);
      }
      setLoading(false);
    }
  }

  async function handleCheckEmail() {
    //se emailCtrl no estiver vazio, ele armazena o email da atualizao 
    //verifica-se se o email na entrada atual  diferente do anterior, 
    //para evitar validar um email existente que j pertence ao usurio atual
    if (emailCtrl === '' || emailCtrl !== email) {
      setLoading(true);
      try {
        const query = await api.get('/user/byemail', {
          data: {},
          params: { email },
          headers: { token }
        });

        const resp = query.data;
        const div = document.getElementById('email');

        if (resp.response) div.setCustomValidity("Email j em uso.");
        else div.setCustomValidity("");

      } catch (error) {
        console.log(error);
      }
      setLoading(false);
    }
  }

  function handleCheckConfirmPassword() {
    const div = document.getElementById('passwordConfirm');

    if (password !== passwordConfirm) div.setCustomValidity('Repita a senha anterior.');
    else div.setCustomValidity("");
  }

  function closeModal() {
    history.goBack()
  }

  async function handleCep() {
    //valor com . (ponto) tambm  um nmero, mas no um cep vlido
    const cepTmp = cep.replace('.', '');
    setCep(cepTmp);

    if (!isNaN(cepTmp) && cepTmp.length === 8) {
      setLoading(true);
      try {
        const resp = (await util.getCep(cepTmp)).data

        if (resp) {
          setStreet(resp.logradouro);
          setComplement(resp.complemento);
          setDistrict(resp.bairro);
          setCity(resp.localidade);
          setState({ value: resp.uf, label: resp.uf });
        }
      } catch (error) {
        console.log(error);
      }
      setLoading(false);
    }
  }

  //avisa usurio sobre um erro ocorrido
  function errorDisplay() {
    Swal.swalErrorInform();
  }

  //ativa/desativa campo de senha e confirmao de senha (atualizao de usurio nao pode inserir senha nova)
  function disableInputPswd(opt) {
    (document.getElementById('password')).disabled = opt;
    (document.getElementById('passwordConfirm')).disabled = opt;
  }

  return (
    <div className="content">
      <Load id="divLoading" loading={loading} />

      <form className="flex-col-h modal-form" autoComplete="off" onSubmit={handleSubmit}>
        <h1 className="title-modal">CADASTRO</h1>

        <div className="flex-row-w">
          <img className="image-profile-large" src={ImageProfile} alt="Foto perfil" />

          <div className="flex-col-h">
            <div className="flex-row-w">
              <div className="flex-col-h" style={{ flex: 3 }}>
                <label htmlFor="name">Nome *</label>
                <input
                  required
                  id="name"
                  placeholder="Nome do usurio"
                  value={name}
                  onChange={event => setName(event.target.value)}
                />
              </div>
              <div className="flex-col-h" style={{ flex: 1 }}>
                <label htmlFor="birth">Data de nascimento *</label>
                <div className="keyboardpicker-modal-taxpayer">
                  <MuiPickersUtilsProvider utils={DateFnsUtils} locale={ptBR}>
                    <KeyboardDatePicker
                      className="nomargin-datepicker"
                      
                      format="dd/MM/yyyy"
                      value={birth}
                      onChange={date => setBirth(date)}
                      KeyboardButtonProps={{
                        'aria-label': 'change date',
                      }}
                      cancelLabel="SAIR"
                    />
                  </MuiPickersUtilsProvider>
                </div>
              </div>
            </div>

            <div className="flex-row-w">
              <div className="flex-col-h">
                <label htmlFor="email">E-Mail *</label>
                <input
                  required
                  type="email"
                  id="email"
                  placeholder="E-mail do usurio"
                  title="E-mail deve ser nico no sistema."
                  value={email}
                  onChange={event => setEmail(event.target.value)}
                  onBlur={handleCheckEmail}
                />
              </div>
              <div className="flex-col-h">
                <label htmlFor="phone">Celular *</label>
                <NumberFormat 
                  required
                  id="phone"
                  value={phone} 
                  onChange={event => setPhone(event.target.value)} 
                  format="(##) # ####-####" 
                  placeholder="(00) 9 0000-0000"
                  title="Telefone com no 9 dgitos."
                  mask="_"
                />
              </div>
            </div>
          </div>
        </div>

        <div className="flex-row-w">
          <div className="flex-col-h">
            <div className="flex-row-w">
              <div className="flex-col-h">
                <label htmlFor="cep">CEP</label>
                <NumberFormat 
                  id="cep"
                  value={cep}
                  onChange={event => setCep(event.target.value)}
                  format="########" 
                  placeholder="00000000"
                  title="Cep do usurio"
                  mask="_"
                  onBlur={handleCep}
                />
              </div>
              <div className="flex-col-h">
                <label htmlFor="street">Rua</label>
                <input
                  id="street"
                  placeholder="Rua"
                  value={street}
                  onChange={event => setStreet(event.target.value)}
                />
              </div>
            </div>

            <div className="flex-row-w">
              <div className="flex-col-h">
                <label htmlFor="number">Nmero</label>
                <input
                  type="number"
                  id="number"
                  placeholder="Nmero"
                  value={number}
                  onChange={event => setNumber(event.target.value)}
                />
              </div>
              <div className="flex-col-h">
                <label htmlFor="complement">Complemento</label>
                <input
                  id="complement"
                  placeholder="Complemento"
                  value={complement}
                  onChange={event => setComplement(event.target.value)}
                />
              </div>
            </div>

            <div className="flex-row-w">
              <div className="flex-col-h">
                <label htmlFor="district">Bairro</label>
                <input
                  id="district"
                  placeholder="Bairro"
                  value={district}
                  onChange={event => setDistrict(event.target.value)}
                />

              </div>
              <div className="flex-col-h">
                <label htmlFor="city">Cidade</label>
                <input
                  id="city"
                  placeholder="Cidade"
                  value={city}
                  onChange={event => setCity(event.target.value)}
                />
              </div>
              <div className="flex-col-h">
                <label htmlFor="state">Estado</label>
                <Select
                  className="select-default"
                  placeholder="Escolha um estado"
                  value={state}
                  onChange={event => setState(event)}
                  options={optState}
                />
              </div>
            </div>
          </div>
        </div>

        <div className="flex-row-w">
          <div className="flex-col-h">

            <label htmlFor="user">Usurio *</label>
            <input
              required
              id="user"
              placeholder="Usurio"
              title="Usurio deve ser nico no sistema."
              value={user}
              onChange={event => setUser(event.target.value)}
              onBlur={handleCheckUser}
            />

            <label htmlFor="password">Senha *</label>
            <input
              required
              pattern=".{8,}"
              id="password"
              type="password"
              placeholder="Senha do usurio"
              title="A senha deve conter no mnimo 8 caracteres."
              value={password}
              onChange={event => setPassword(event.target.value)}
            />

            <label htmlFor="passwordConfirm">Confirmar senha *</label>
            <input
              required
              id="passwordConfirm"
              type="password"
              placeholder="Confirmar senha"
              title="Repita a senha anterior."
              value={passwordConfirm}
              onChange={event => setPasswordConfirm(event.target.value)}
              onBlur={handleCheckConfirmPassword}
            />

            <label className="switch-report-search">
              <Switch
                onChange={() => setIsAdm(!isAdm)}
                checked={isAdm}
                onColor='#0e78fa'
              />
              <span>Usurio do tipo <strong>{isAdm ? 'Admnistrador' : 'Comum'}</strong></span>
            </label>
          </div>

        </div>

        <div className="flex-row-w modal-form-btn">
          <button type="button" onClick={closeModal} className="btn-cancel">CANCELAR</button>
          <button type="submit" className="btn-ok">SALVAR</button>
        </div>
      </form>
    </div>
  )
}